import React, { Component } from 'react';
class CountryList extends Component {
  renderCountry = each => {
    return (
      <li
        className="items"
        key={each.currency}
        onClick={this.handleClick.bind(this, each.currency)}
      >
        {each.country}
      </li>
    )
  }
  render() {
    return (
      <ul>{this.props.countries.map(each => this.renderCountry(each))}</ul>
    )
  }
}
export default CountryList;